@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700|Raleway:400,600,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
	margin: 0;
	padding: 0;
}
a{
	/*color: #fff;*/
	text-decoration: none;
}
a:hover,
a:focus{
	color: #ff0066;
	text-decoration: none;
	outline: none;
}
h1,h2,h3,h4,h5,h6{
	font-family: 'Poppins', sans-serif;
	margin: 0;
	padding: 0;
}
h1{
	font-size: 64px;
	font-weight: bold;
}
h2{
	font-size: 48px;
	font-weight: bold;
}
h3{
	font-size: 30px;
	font-weight: 500;
}
h4{
	font-size: 24px;
	font-weight: 500;
}
h5{
	font-size: 18px;
	font-weight: 500;
}
h6{
	font-size: 16px;
}
p{
	margin: 0;
	padding: 0;
}
ul{
	margin: 0;
	padding: 0;
}
li{
	list-style: none;
}

.header{display:flex;align-items:center;max-width:1040px;flex-wrap:wrap;margin:0px auto;padding:0px 40px}.header .img{width:50px;height:50px;border-radius:50%;border-style:none}.header .title{letter-spacing:-0.2px;line-height:1.4;padding-top:3px;margin:10px 20px}.header .social{display:flex;align-items:center;justify-content:flex-end;flex-grow:1}.header li{display:inline-block;margin-left:30px}.header .links-wrapper{display:flex}
.contact-info-wraper{height:100px;padding-top:15px;padding-bottom:15px}.contact-info-wraper .contact-links{display:flex;grid-gap:4rem;gap:4rem}
.intro-wrapper{margin-left:auto;margin-right:auto;width:50%;padding:30px}

.experiene-box{border-left:3px solid #00bcd4;border-bottom-right-radius:2px;border-top-right-radius:2px;position:relative;padding:0 30px;margin-left:280px}.experiene-box .item{position:relative;margin-bottom:45px}.experiene-box .designation{position:absolute;left:-280px;top:0}.experiene-box .info{margin-top:0;margin-bottom:15px;position:relative}.experiene-box .place{position:absolute;right:0;top:2px}
.skill-title{max-width:700px;text-align:center;margin-left:auto;margin-right:auto;margin-bottom:3rem}
.contact-wrapper{text-align:center}.contact-wrapper .img{width:80px;height:80px;border-radius:50%;border-style:none}.contact-wrapper .info{width:780px;margin-left:auto;margin-right:auto}
